// 
$black: #333;

/* $theme-colors */
$primary: #7e0f83;
$secondary: #364799;
$success: #1F501D;
$danger: #BE1A1C;
$warning: #FDB448;
$info: #cea6cc;
$light: #f8f9fa;
$dark: #212529;

/*$theme-colors */
$theme-colors: (
  "primary": $primary,
  "secondary": $secondary,
  "success": $success,
  "info": $info,
  "warning": $warning,
  "danger": $danger,
  "light": $light,
  "dark": $dark
  );

/*$custom-colors */
$custom-colors: (
  "blue-light": #E1F3FF,
  "blue-main": #1F5AD9,
  "blue-dark": #184880,
  "accent": #FDB448
);

/*map-merge   $theme-colors  $custom-colors */
$theme-colors: map-merge($theme-colors, $custom-colors);

/* Bootstrap  Sass  */
@import "~bootstrap/scss/bootstrap.scss";

// scss
@import "./scss/foundation/spacer";